.playingVideo .title {
    margin-top: 1rem;
}

.playingVideo .transcript {
    color: white;
    font-size: 20px;
    /*font-family: BreitkopfFraktur;*/
}

.playingVideo video {
    width: 100%;
    border-radius: .5rem;
}

.video img {
    position: absolute;
    left: 1rem;
    top: 50%;
    transform: translateY(-50%);

    width: 1.5rem;
    height: 1.5rem;

    filter: invert(100%);
}

.video p {
    margin-left: 2.5rem;
}

.video h3 {
    width: 23rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    padding: 0 .5rem;
}
